@use "../../../Public/SCSS/Utilities/Mixins" as *;
@use "../../../Public/SCSS/Utilities/Variables" as *;

////////////////////////////////////////////////
//  Master Grid Container
.master-grid {
  .master-flex-container {
    /////////////////
    // About Page
    .about-me {
      @include SizeAndPositioning(relative, 100%, 100%, flex, center, center, 0, 0, 0);
      flex-direction: column;
      overflow-y: auto;
      &__header {
        @include SizeAndPositioning(relative, 40%, 100%, flex, space-evenly, center, 0, 0, 0);
        flex-direction: column;
      }
      &__introductory-paragraph {
        @include SizeAndPositioning(relative, 15%, 75%, flex, center, center, 0, 0, 0);
      }
      &-viewport {
        @include SizeAndPositioning(relative, 45%, 75%, flex, space-evenly, center, 0, 0, 0);
        flex-direction: column;
        .about-me-statement-slider {
          @include SizeAndPositioning(relative, 75%, 100%, flex, space-evenly, center, 0, 0, 0);
          &-viewport {
            @include SizeAndPositioning(relative, 100%, 60%, flex, center, center, 0, 0, 0);
            overflow: hidden;
            .about-me-statement {
              @include SizeAndPositioning(absolute, 80%, 100%, flex, center, center, 0, 0, 0);
              margin: 0 2rem;
              padding: 0 2rem;
              transition: transform 1s;
              &:first-of-type {
                margin: 0 2rem 0 0;
              }
              &:last-of-type {
                margin: 0 0 0 2rem;
              }
            }
          }
        }
        .about-me-statement-slider-navigation {
          @include SizeAndPositioning(relative, 25%, 100%, flex, center, center, 0, 0, 0);
          &__dot {
            @include SizingItem(relative, 3rem, 3rem);
            background-color: rgba($colorGrey, 0.5);
            border: none;
            border-radius: 50%;
            margin: 0 1rem;
            transform: translate(0, -6rem);
            &--active {
              background-color: $colorDarkerGrey;
            }
          }
        }
      }
    }
  }
}
