@use "../../../Public/SCSS/Utilities/Mixins" as *;
@use "../../../Public/SCSS/Utilities/Variables" as *;

////////////////////////////////////////////////
//  Master Grid Container
.master-grid {
  .master-flex-container {
    /////////////////
    // Contact Page
    .form-container {
      @include SizeAndPositioning(relative, 79%, 85%, flex, space-between, space-evenly, 0, 0, 0);
      background: url('/DIST/CSS/Images/Letter-Border.svg');
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      padding: 4rem;
      box-shadow: 0 0 1rem 0.35rem $colorDarkGrey;
      flex-direction: column;
      &__requirement-reminder {
        @include SizeAndPositioning(absolute, max-content, max-content, flex, center, center, 2rem, -3rem, 0);
        top: 0;
        left: 0;
      }
      &__form-header {
        @include SizeAndPositioning(relative, 25%, 100%, flex, center, space-evenly, 0, 0, 0);
        &__addressed {
          @include SizeAndPositioning(absolute, max-content, max-content, flex, center, center, 0, 0, 0);
          top: 6rem;
          left: 8rem;
        }
        .stamp {
          @include SizeAndPositioning(absolute, 20rem, auto, none, center, center, 0, 0, 0);
          top: 0;
          right: 1rem;
          &--active {
            display: flex;
          }
        }
      }
      .form-response {
        @include SizeAndPositioning(absolute, max-content, max-content, flex, center, center, -2rem, 3rem, 0);
        bottom: 0;
        right: 0;
      }
    }
  }
}
